/* ===============================
üåû È°∂ÈÉ®ÂØºËà™Ê†èÔºàÊúÄÁªà‰ºòÂåñÁâàÔºâ
‰øùÁïô‰Ω†ÂéüÊúâÈÖçËâ≤‰∏éÊ†∑ÂºèÔºåÂè™‰ºòÂåñÈó¥Ë∑ù‰∏éÁªìÊûÑÂ±ÇÊ¨°
================================ */
.site-header {
  position: sticky;
  top: 0;
  width: 100%;
  background: linear-gradient(135deg, #fffae6, #fff2b5);
  box-shadow: 0 4px 12px rgba(255, 210, 80, 0.3);
  z-index: 100;
  font-family: "Segoe UI", sans-serif;
}

.header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 48px; /* ‚úÖ Â∑¶Âè≥Êõ¥ÂÆΩÔºåËèúÂçï‰∏çÂÜçË¥¥ */
  max-width: 1200px;
  margin: 0 auto;
  gap: 40px; /* ‚úÖ ‰∏≠Èó¥ÂëºÂê∏Á©∫Èó¥ */
}

/* üåû Logo */
.logo {
  font-size: 22px;
  font-weight: 700;
  color: #ff9800;
  cursor: pointer;
  transition: 0.2s;
  white-space: nowrap;
}

.logo:hover {
  transform: scale(1.05);
}

/* üß≠ Ê°åÈù¢ËèúÂçï */
.nav-links {
  display: flex;
  gap: 40px; /* ‚úÖ ËèúÂçïÈó¥Ë∑ùÂ¢ûÂ§ß */
  flex: 1;
  justify-content: center; /* ‚úÖ Â±Ö‰∏≠Êõ¥ÂçèË∞É */
}

.nav-links button {
  background: none;
  border: none;
  font-size: 18px;
  font-weight: 600;
  color: #444;
  cursor: pointer;
  transition: 0.3s;
}

.nav-links button:hover {
  color: #ff9800;
  transform: scale(1.08);
}

/* üë§ ÁôªÂΩïÊ≥®ÂÜå */
.auth-section {
  display: flex;
  align-items: center;
  gap: 20px; /* ‚úÖ ÁôªÂΩïÂíåËèúÂçïÈó¥ÁïôÁôΩ */
}

.login-btn,
.logout-btn {
  background: linear-gradient(135deg, #ffd452, #ffb347);
  border: none;
  border-radius: 10px;
  padding: 8px 18px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  color: #333;
  transition: 0.3s;
}

.login-btn:hover,
.logout-btn:hover {
  transform: scale(1.05);
}

/* üì± ÁßªÂä®Á´ØËèúÂçï */
.menu-toggle {
  display: none;
  font-size: 28px;
  background: none;
  border: none;
  cursor: pointer;
  color: #555;
}

/* ===============================
üì± ÁßªÂä®Á´ØÂìçÂ∫îÂºè
================================ */
@media (max-width: 768px) {
  .header-container {
    flex-wrap: wrap;
    justify-content: center;
    padding: 12px 20px;
    gap: 16px;
  }

  .nav-links {
    display: none;
    position: absolute;
    top: 64px;
    right: 10px;
    flex-direction: column;
    background: #fff8dc;
    border-radius: 12px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
    padding: 16px;
    gap: 12px;
    z-index: 200;
  }

  .nav-links.open {
    display: flex;
  }

  .menu-toggle {
    display: block;
  }
}

/* ===============================
üåà Âä®Áîª
================================ */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
